.Container_header{
    background-color: #282c34;
}
.Header_box{
    max-width: 1280px;
    margin: 0 auto;
    padding: 15px;
    color: azure;
    display: flex;
    justify-content: space-between;
}
.Header_logo {
    height: 100px;
    pointer-events: none;
}
@media (prefers-reduced-motion: no-preference) {
    .Header_logo {
      animation: Header_logo-spin infinite 20s linear;
    }
}
@keyframes Header_logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
}
.Header_list{
    width: 50%;
    color: azure;
    font-size: 36px;
    font-weight: 600;
    display: flex;
    justify-content: space-around;
    align-items: center;
}